import Box from '@material-ui/core/Box';
import React, { Dispatch, SetStateAction, Suspense, useState } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { Logs } from './pages/logs/Logs';
import { ILog } from './interfaces/ILog';
import { Tracker } from './pages/tracker/Tracker';
import { repository } from './repositories/localStrage';
import { Header } from './components/molecules/header/Header';
import { Footer } from './components/molecules/footer/Footer';
import { Policy } from './pages/policy/Policy';
import { Settings } from './pages/settings/Settings';

function PageTemplate(props: { inner: () => JSX.Element }) {
    return <Box display="flex" flexDirection="column" justifyContent="space-between" height="100vh">
        <Header />
        <Box flexGrow="1">
            {props.inner()}
        </Box>
        <Footer />
    </Box>
}

export function Sink() {
    const [logs, setLogs]: [ILog[], Dispatch<SetStateAction<ILog[]>>] = useState(repository.getLogs)

    return (
        <BrowserRouter>
            <Suspense fallback={<div>Loading...</div>}>
                <Switch>
                    <Route exact path="/sink/tracker" render={() => <PageTemplate inner={() => <Tracker repo={repository} setLogs={setLogs} logs={logs} />} />} />
                    <Route exact path="/sink/logs" render={() => <PageTemplate inner={() => <Logs logs={logs} />} />} />
                    <Route exact path="/sink/policy" render={() => <PageTemplate inner={() => <Policy />} />} />
                    <Route exact path="/sink/settings" render={() => <PageTemplate inner={() => <Settings setLogs={setLogs} repo={repository} />} />} />
                    <Route path="/sink/" render={() => <PageTemplate inner={() => <Tracker repo={repository} setLogs={setLogs} logs={logs} />} />} />
                </Switch>
            </Suspense>
        </BrowserRouter>
    );
}
