import React, { Dispatch, SetStateAction, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Task } from '../../components/atoms/task/Task';
import { ILog } from '../../interfaces/ILog';
import { IRepository } from '../../interfaces/IRepository';


export function Tracker(props: { repo: IRepository, setLogs: Dispatch<SetStateAction<ILog[]>>, logs: ILog[] }) {
    const { repo, setLogs, logs } = props
    const [taskName, setTaskName] = useState('')
    const [time, setTime] = useState(repo.getTime())
    const [currentTaskName, setCurrentTaskName] = useState(repo.getCurrentTaskName())
    const [history, setHistory]: [string[], Dispatch<SetStateAction<string[]>>] = useState(repo.getHistory())

    const handleChange_taskName = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTaskName(event.target.value);
    };

    const act = (taskName: string): void => {
        setCurrentTaskName(taskName)
        repo.setCurrentTaskName(taskName)

        const time = Date.now()
        setTime(time)
        repo.setTime(time)

        setTaskName('')
    }

    const addHistory = (taskName: string): void => {
        const newHistory = [taskName, ...history.filter(v => v !== taskName)].slice(0, 10)

        setHistory(newHistory)
        repo.setHistory(newHistory)
    }

    const switchTask = (taskName: string): void => {
        if (currentTaskName === taskName || taskName === '') { return setTaskName('') };

        if (currentTaskName !== '') { stopTask() }

        act(taskName)

        addHistory(taskName)
    }

    const stopTask = (): void => {
        const stopTime = Date.now()
        const log: ILog = {
            taskName: currentTaskName,
            start: time,
            end: stopTime,
        }
        const newLog = [log, ...logs]

        setLogs(newLog)
        repo.setLogs(newLog)

        setCurrentTaskName('')
        repo.setCurrentTaskName('')
    }

    const handleClick_actButton = () => {
        switchTask(taskName)
    }
    const handleClick_playButton = (taskName: string) => {
        switchTask(taskName)
    }
    const handleClick_stopButton = () => {
        stopTask()
    }


    return (
        <Box display="flex" className="Tracker">
            <Box display="flex" flexDirection="column">
                <Box display="flex" height="36px">
                    <Box width="64px">
                        <Button onClick={handleClick_stopButton} disabled={currentTaskName === ''}><span className="material-icons">stop</span></Button>
                    </Box>
                    <Box marginY="auto">
                        <Box>Task: {currentTaskName !== '' ? currentTaskName : 'break now...ğŸµ'}</Box>
                    </Box>
                </Box>
                <Box display="flex" marginY="16px">
                    <Button onClick={handleClick_actButton}>act</Button>
                    <TextField
                        id="standard-basic"
                        value={taskName}
                        onChange={handleChange_taskName}
                        label="new task" />
                </Box>
                <Box display="flex" flexDirection="column">
                    {history.map(t => <Task key={t} taskName={t} handler={handleClick_playButton}></Task>)}
                </Box>
            </Box>
        </Box>
    );
}
