import React from 'react';
import Button from '@material-ui/core/Button'
import Box from '@material-ui/core/Box'
// import './Task.css';

export function Task(props: { taskName: string, handler: (taskName: string) => void }) {
    const { taskName, handler } = props
    return (
        <Box className="task" display="flex" justifyContent="space-between">
            <Button onClick={() => { return handler(taskName) }}>
                <span className="material-icons">play_arrow</span>
            </Button>
            <Box flexGrow="1" margin="auto"><Box>{taskName}</Box></Box>
        </Box>
    );
}
