import { IRepository } from '../interfaces/IRepository'
import { ILog } from '../interfaces/ILog'

export const repository: IRepository = {
    setTime: (time: number) => {
        localStorage.setItem('time', `${time}`)
    },
    getTime: () => {
        return parseInt(localStorage.getItem('time') || '0')
    },
    setCurrentTaskName: (currentTaskName: string) => {
        localStorage.setItem('currentTaskName', currentTaskName)
    },
    getCurrentTaskName: () => {
        return localStorage.getItem('currentTaskName') || ''
    },
    setIsWorking: (isWorking: boolean) => {
        localStorage.setItem('isWorking', `${isWorking}`)
    },
    getIsWorking: () => {
        return localStorage.getItem('isWorking') === 'true'
    },
    setHistory: (history: string[]) => {
        localStorage.setItem('history', JSON.stringify(history))
    },
    getHistory: () => {
        return JSON.parse(localStorage.getItem('history') || '[]')
    },
    setLogs: (logs: ILog[]) => {
        localStorage.setItem('logs', JSON.stringify(logs))
    },
    getLogs: () => {
        return JSON.parse(localStorage.getItem('logs') || '[]')
    },
}